import mixins from "~/utils/mixins";
import {common} from "~/utils/api/url";
import {
  cityInfo,
  educationInfo,
  MessageError,
  MessageSuccess,
  nationalityInfo,
  nationInfo,
  provinceInfo
} from "~/utils/utils";
import MyHeadImg from "~/components/utils/common/MyHeadImg";

  export default {
    components: {MyHeadImg},
    mixins: [mixins],
    data(){
      return {
        tableData: [],
        tableSignData: [],
        countNum: 0,
        signNum: 0,
        unSignNum: 0,
        collegeList: [],
        categorys: [],
        types: [],
        subTitle: '',
        collegeData: '',
        searchKey: '',
        searchType: '',
        searchStatus: '',
        searchTopType: '',
        searchTopStatus : '',
        searchCascader: [],
        typeOptions: [
          {value: 0, text: this.$t("人工"), label: this.$t("人工")},
          {value: 1, text: this.$t("二维码"), label: this.$t("二维码")},
          {value: 2, text: this.$t("人脸识别"), label: this.$t("人脸识别")}
        ],
        statusOptions: [
          {value: true, text: this.$t("成功"), label: this.$t("成功")},
          {value: false, text: this.$t("失败"), label: this.$t("失败")}
        ],
        searchTimeData: [],
        searchAuditType: '',
        searchSexType: '',
        searchSignTimeStatus: '',
        searchInterface: '',
        dialogVisible: false
      }
    },
    created() {
      this.init();
    },
    methods: {
      init(){
        let params = {
          page: this.page,
          num: this.num,
          collegeId: this.searchCollege,
          majorId: this.searchMajor,
          grade: this.searchGrade,
          classId: this.searchClass,
          searchKey: this.searchKey,
          checkType: this.searchTopType,
          checkStatus: this.searchTopStatus,
          startTime: (this.searchTimeData && this.searchTimeData.length > 0) ? this.$moment(this.searchTimeData[0]).format("YYYY-MM-DD") : '',
          endTime: (this.searchTimeData && this.searchTimeData.length > 0) ? this.$moment(this.searchTimeData[1]).format("YYYY-MM-DD") : '',
        };
        this.$axios.get(common.enroll_checkin_record_page, {params: params}).then(res => {
          if (res.data.data){
            this.tableData = res.data.data.list;
            this.total = res.data.data.totalCount;
            this.num = res.data.data.num;
            this.page = res.data.data.currentPage;
          }
        });
      },
      sizeChange(event){
        this.page = 1;
        this.num = event;
        this.init();
      },
      currentPage(event){
        this.page = event;
        this.init();
      },
      jumpPage(data){
        this.page = data;
        this.init();
      },
      nodeClick(data){
        this.searchCollege = "";
        this.searchMajor = "";
        this.searchGrade = "";
        this.searchClass = "";
        if (data.unit == 1){
          this.searchCollege = data.id;
        }else if (data.unit == 2){
          this.searchCollege = data.college_id;
          this.searchMajor = data.id;
        }else if (data.unit == 3){
          this.searchMajor = data.major_id;
          this.searchGrade = data.grade;
        }else if (data.unit == 4){
          this.searchClass = data.id;
        }
        this.page = 1;
        this.init();
      },
      handleCascaderChange(data){
        this.searchCascader = data;
      },
      handleChangeTime(data){
        this.searchTimeData = data ? data : [];
      },
      search(data){
        this.searchKey = data.input;
        this.page = 1;
        this.init();
      },
      expandInfo() {
        let url = common.housework_query_class_record_export;
        let params = {
          page: this.page,
          num: this.num,
        };
        params = this.$qs.stringify(params);
        window.open(url + "?" + params, "_self");
      },
      changeStatus(type){
        this.searchType = type.value;
        this.page = 1;
        this.init();
      },
      statusInfo(item, type){

      },
      detailInfo(item, type){
        this.form.id = 1;
        this.dialogDetail = true;
      },
      signInfo($event, item){
        this.dialogVisible = true;
      },
      fliterTable(value, row, column){
        for (let item in value){
          if (item == 'auditType'){
            this.searchAuditType = value[item][0];
          }else if (item == 'sexType'){
            this.searchSexType = value[item][0];
          }else if (item == 'signType'){
            this.searchSignTimeStatus = value[item][0];
          }else if (item == 'interfaceType'){
            this.searchInterface = value[item][0];
          }
        }
        this.init();
      },
      nationInfoText(){
        let arr = nationInfo();
        for (let i = 0; i < arr.length; i++){
          arr[i]['label'] = arr[i].name;
          arr[i]['value'] = arr[i].code;
        }
        return arr;
      },
      nationalityInfoText(){
        let arr = nationalityInfo();
        for (let i = 0; i < arr.length; i++){
          arr[i]['label'] = arr[i].country_name_cn;
          arr[i]['value'] = arr[i].country_code;
        }
        return arr;
      },
      educationInfoText(){
        let arr = educationInfo();
        for (let i = 0; i < arr.length; i++){
          arr[i]['label'] = arr[i].name;
          arr[i]['value'] = arr[i].value;
        }
        return arr;
      },
      provinceInfoText(){
        let arr = provinceInfo();
        for (let i = 0; i < arr.length; i++){
          arr[i]['label'] = arr[i].name;
          arr[i]['value'] = arr[i].ProID;
        }
        return arr;
      },
      cityInfoText(){
        let arr = cityInfo();
        for (let i = 0; i < arr.length; i++){
          arr[i]['label'] = arr[i].name;
          arr[i]['value'] = arr[i].CityID;
        }
        return arr;
      },
      handleTypeChange(event, type){
        if (type == 1){
          this.searchTopType = event;
        }else if (type == 2){
          this.searchTopStatus = event;
        }
      },
      closeDialog(event){
        this.form = {
          id: '',
          year: '',
          name: '',
          adNo: '',
          oneCardNo: '',
          stuNo: '',
          examNo: '',
          sex: '',
          idCardNo: '',
          birthday: '',
          nationality: '',
          nation: '',
          phone: '',
          qq: '',
          wechat: '',
          fatherName: '',
          fatherPhone: '',
          motherName: '',
          motherPhone: '',
          address: '',
          education: '',
          college: [],
          major: '',
          class: [],
          eduSystem: '',
          headmaster: '',
          recruitingTeacher: '',
          adBath: '',
          subject: '',
          adProvince: '',
          adCity: '',
          graduationSchool: '',
          examScore: '',
          otherMsg: ''
        };
        this.subTitle = "";
        this.versionStatus = '';
        this.resetCasadeSelector('SelectorCollege');
        this.resetCasadeSelector('selectorDept');
        if (this.$refs['form']){
          this.$refs['form'].resetFields();
        }
        this.uploadProcess = '';
        this.uploadResult = [];
        clearTimeout(this.loopTimer);
        this.loopTimer = null;
        this.teacherArray = [];
        this.approverUsers = [];
        this.btnLoading = false;
        this.dialogVisible = false;
        this.dialogDetail = false;
      },
      cancelFormDrawDialog(){
        this.closeDialog();
        this.drawerForm = false;
      },
      okFormDrawDialog(event, type){
        let params = {

        };

        let url = common.circular_add;
        //params = this.$qs.stringify(params);
        this.btnLoading = true;
        this.$axios.post(url, params, {loading: false}).then(res => {
          if (res.data.code == 200){
            this.drawerManage = false;
            this.init();
            this.closeDialog();
            MessageSuccess(res.data.desc);
          }else {
            MessageError(res.data.desc);
          }
          this.btnLoading = false;
        });
      }
    }
  }
